@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 5px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  background-color: lightgrey;
  display: flex;
  flex-direction: column;
}
.app__body {
  display: flex;
}

.inputOption:hover {
  background-color: whitesmoke;
  border-radius: 10px;
  padding-top: 5px;
  cursor: pointer;
}

.inputOption {
  align-items: center;
  margin-top: 5px;
  color: gray;
  cursor: pointer;
}

.feed {
  flex: 0.75 1;
  margin: 0 20px;
}

.feed__inputContainer {
  background-color: white;
  padding: 10px;
  padding-bottom: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.feed__input {
  border: 3px solid lightgray;
  border-radius: 30px;
  display: flex;
  padding: 3px;
  color: gray;
  padding-left: 15px;
}

.feed__input > form {
  display: flex;
  width: 100%;
}

.feed__input > input {
  border: none;
  flex: 1 1;
  margin-left: 10px;
  outline-width: 0;
  font-weight: 600px;
}
.feed__input > button {
  width: 100px;
  border: none;
  background-color: rgb(200, 199, 104);
  height: 30px;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top-right-radius: 15px;
  font-size: 15px;
  color: #ae2419;
}
.feed__input > form > button:hover {
  width: 120px;
  color: brown;
  background-color: rgb(44, 72, 107);
  font-size: 20px;
}
.feed__inputOptions {
  display: flex;
  justify-content: space-evenly;
  margin-top: 10px;
  text-align: center;
}

.post {
  background-color: white;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 10px;
}

.post__header {
  display: flex;
  margin-bottom: 10px;
}

.post__info {
  margin-left: 10px;
}
.post__info > p {
  font-size: 12px;
  color: gray;
}

.post__body {
  overflow-wrap: anywhere;
}

.post__info > h2 {
  font-size: 15px;
}

.post__buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  text-align: center;
}

.header {
  display: flex;
}
.header__left {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-evenly;
  border-bottom: 0.5px solid lightgray;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  z-index: 999;
  background-color: rgb(248, 238, 195);
}

.header__left > img {
  object-fit: contain;
  height: 80px;
  margin-right: 10px;
}

.header__search {
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  width: 400px;
  height: 45px;
  color: gray;
  background-color: #eef3f8;
}

.header__search > input {
  outline: none;
  border: none;
  background: none;
}

.headerOption {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  color: gray;
  cursor: pointer;
}

.headerOption__title {
  font-size: 12px;
  font-weight: 400;
}

.headerOption__icon {
  object-fit: contain;
  height: 25px !important;
  width: 25px !important;
}

.headerOption:hover {
  color: black;
  padding-top: 5px;
}

.header__right {
  display: flex;
}

.sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
  flex: 0.35 1;
  border-radius: 10px;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.sidebar__avatar {
  margin-bottom: 10px;
}
.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}
.sidebar__top > img {
  margin-bottom: -20px;
  width: 100%;
  height: 60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}

.sidebar__stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.sidebar__stat > p {
  color: gray;
  font-size: 12px;
  font-weight: 600;
}
.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid lightgrey;
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.sidebar__top > h4 {
  color: grey;
  font-size: 12px;
}
.sidebar__top > h2 {
  font-size: 18px;
}

.sidebar__statNumber {
  font-weight: bold;
  color: rgba(116, 36, 36, 0.692) !important;
}
.sidebar__bottom {
  text-align: left;
  padding: 10px;
  border: 1px solid lightgrey;
  background-color: white;
  border-radius: 10px;
  margin-top: 10px;
}

.sidebar__bottom > p {
  font-size: 13px;
  padding-bottom: 10px;
}

.sidebar__groups {
  display: flex;
  font-size: 13px;
  color: gray;
  cursor: pointer;
  font-weight: bold;
  padding: 3px;
  margin-bottom: 5px;
}

.sidebar__groups:hover {
  background-color: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
  color: black;
}

.signupform {
  background-color: rgb(242, 250, 250);
}

.dreams_body {
  background-color: lightgrey;
  display: flex;
  flex-direction: column;
}

h1 {
  font-family: "Permanent Marker", cursive;
  color: rgb(192, 101, 45);
}

h2 {
  font-size: 1.5em;
  text-align: center;
}
.dream_sidebar {
  display: flex;
}
.feed {
  overflow-wrap: anywhere !important;
}

.dreams_content {
  display: flex;
  justify-content: space-evenly;
}

.dreams {
  display: flex;
}
.dream_box > div {
  display: flex;
  flex-direction: row;
  margin: 20px;
  border-radius: 10px;
  border: 2px solid rgba(228, 132, 132, 0.918);
}
.dream_box > div > img {
