  border-radius: 10px;
  margin-right: 5px;
}

.dream_list > p {
  color: rgb(192, 101, 45);
  text-decoration: none;
}

.btn_control {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  margin-left: auto;
}

#top-section-home {
  width: 100%;
  height: 800px;
  text-align: center;
  position: relative;
}

#home-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

#home-h1 {
  -webkit-animation: apparition 0.8s ease-out;
          animation: apparition 0.8s ease-out;
  width: 80%;
  text-align: center;
  font-size: 40px;
  font-weight: 900;
  color: rgba(0, 0, 0, 0.908);
}

#home-p {
  font-size: 20px;
  width: 50%;
  -webkit-animation: apparition 1s ease-out;
          animation: apparition 1s ease-out;
}

#top-section-btn {
  padding: 10px 25px;
  border: 4px solid #773964;
  border-radius: 10px;
  text-decoration: none;
  background-color: #a15a58;
  color: rgba(0, 0, 0, 0.74);
  margin-top: 20px;
  -webkit-animation: apparition 1s ease-out;
          animation: apparition 1s ease-out;
}

#top-section-title {
  width: 50%;
  position: absolute;
  top: 20%;
  max-height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#top-color-image {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(218, 216, 216, 0.553);
  border-radius: 10px;
  width: 80%;
  padding: 20px;
}

#middle-container {
  height: 100%;
  margin-top: 50px;
}

#middle-container h2 {
  text-align: center;
  font-size: 30px;
}

#grid-section {
  display: grid;
  grid-template-columns: repeat(auto-fill, 650px);
  height: auto;
  width: 100%;
  margin: auto;
  max-width: 1300px;
  background-color: rgb(97, 149, 112);

  margin-top: 50px;
}

#grid-dev {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px;
}

#grid-dev h3 {
  font-size: 40px;
  -webkit-animation: slide 1s ease-out;
          animation: slide 1s ease-out;
  color: #788bff;
}

#grid-dev p {
  font-size: 20px;
  color: #fbf9f3ad;
}

.middle-container-downer {
  height: 200px;
  margin-top: 50px;
  text-align: center;
}

.middle-container-downer p {
  font-style: italic;
  font-size: 25px;
  margin-bottom: 60px;
}

@-webkit-keyframes slide {
  from {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes slide {
  from {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@-webkit-keyframes apparition {
  from {
    opacity: 0;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes apparition {
  from {
    opacity: 0;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@media screen and (min-width: 901px) {
  #home-h1 {
    font-size: 40px;
  }
  #top-section-title {
    width: 50%;
  }
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 900px) {
  #home-h1 {
    font-size: 20px;
  }
  #top-section-title {
    width: 100%;
  }
}

